body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.word {
  border-radius: .5rem;
  border: 1px solid rgba(0,0,0,.1);
  padding: 3px 10px 3px 10px;
  text-align: center;
  font-size: .75rem;
  margin-right: 3px;
  margin-bottom: 3px;
}

.card-link--nostyle:link{
  text-decoration: none;
  color: black;
}
.card-link--nostyle:visited{
  text-decoration: none;
  color: black;
}
.row {
  display: flex;
  justify-content: flex-start;
  margin-left: 0rem;
  margin-top: 0.5rem;
}
.custom.card {
  margin: 5px 5px 5px 5px;
  max-width: 400px;
}
.custom.card:hover {
  /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.07); */
  box-shadow: 2px 3px rgba(0,0,0,0.7);
  border-color: rgba(0,0,0,0.7);
  border-width: 1px;
}
.custom.card > .card-body > .title.card-link {
  font-size: 1.2rem;
  /* color: #000000; */
  color: #56CCF2;
}
.custom.card > .card-body > .card-subtitle > .subtitle.card-link {
  margin-top: 1px;
  font-size: 0.9rem;
  color: rgba(0,0,0,0.4);
}
.custom.card > .card-body > .title.card-link:hover {
  font-size: 1.2rem;
  color: #2D9CDB;
  /* color: #2F80ED; */
}
.custom.card > .card-body > .card-subtitle > .subtitle.card-link:hover {
  color: rgba(0,0,0,0.7);
}
.custom.card > .card-body > .card-subtitle {
  font-size: 0.9rem;
  color: rgba(0,0,0,0.7);
}

.results {
  padding: 20px 20px 20px 20px;
  position: element(#searchbar);
  left: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  flex-direction: row;
}

.searchbox--results.form-control{
  width: 35vw;
  min-width: 100px;
  /* border-radius: 5px; */
  font-size:1rem;
}

.searchbox--results.form-control:focus,
.searchbox--results.form-control:hover{
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.07);
    /* border: 1px solid #ced4da; */
    border: 1px solid #000000;
  /* border-color: #56CCF2; */
}

.header.navbar.navbar-light.bg-faded {
  border-bottom: 0.5px solid rgba(0,0,0,0.2);
  padding: 1.5rem 2rem;
  background-color: white;
  /* border-color: #2F80ED; */
}

.header .navbar-brand {
  font-size: 1.7rem;
  margin-right: 1.5rem;
}

.App {
  display: flex;
  flex-wrap:wrap;
  justify-content: center;
  align-items: center;
  width:100vw;
  height:100vh;
}
.App h1{
  font-size:5.5rem;
}
.searchbox--main.form-control{
  width: 40vw;
  min-width: 300px;
  border-radius: 30px;
  font-size:1rem;
}

.searchbox--main.form-control:focus,
.searchbox--main.form-control:hover{
  box-shadow: 3px 4px rgba(0,0,0,0.7);
  border-color: rgba(0,0,0,0.7);
  border-width: 2px;
}
/* .searchbox--main.form-control:hover{
  box-shadow: 3px 4px;
  border-color: #101010;
  border-width: 3px;
} */

.App .btn-outline-primary {
  margin-left: 10px;
  background-color: rgba(86, 204, 242, 0.05);
  border-color: rgba(86, 204, 242, 1);
  color: rgba(86, 204, 242, 1);
  font-size: 0.8rem;
}
.App .btn-outline-primary:hover {
  background-color: rgba(86, 204, 242, 0.15);
  border-color: rgba(86, 204, 242, 1);
  color: rgba(86, 204, 242, 1);
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.07);
}
.App .btn-outline-primary:active:focus {
  background-color: rgba(86, 204, 242, 0.15);
  border-color: rgba(86, 204, 242, 1);
  color: rgba(86, 204, 242, 1);
}

.alphabet {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  flex-direction: row;
  justify-content: flex-start;
}
.alphabet button {
  max-width: 100px;
  margin-right: 10px;
}

.btn-secondary.disabled, .btn-secondary:disabled {
  margin-top: .5rem;
}

